import React from 'react'
import ViewTable from './ViewTable/ViewTable'
import UpdateTable from './EditTable'
import Button from '@material-ui/core/Button'
import Modal from '@material-ui/core/Modal'
import classes from './index.module.scss'
import Columns from './Utilities/viewColumns'
import UpdateColumns from './Utilities/updateColumns'
import AddRoomType from './Utilities/AddRoomType'
import isEmpty from 'lodash/isEmpty'
import CustomDeleteSnackbar from '../../../../Components/CustomSnackBar/CustomAlertDelSnackBar'
import CustomAlerSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import CancelConfirmation from './Utilities/CancelConfirmation'
import CustomConfirmationDialog from '../../../../Components/CustomConfirmationDialog/CustomConfirmationDialog'
import ConfirmationBox from './Utilities/ConfirmationBox'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'
import RoomTypeService from '../../../../../../../Services/Api/RoomType/RoomType'
import { getNestedObjects } from '../../../../Components/Utils/utils'

const Index = props => {
  const { paramId, open, setOpen } = props

  const [form, setForm] = React.useState({
    roomCode: '',
    roomType: '',
    description: '',
    price: '',
    status: '',
    remarks: ''
  })

  const [isDelete, setDelete] = React.useState(false)
  const [indexTodelete, setIndexToDelete] = React.useState(null)
  const [passIndex, setPassIndex] = React.useState(-1)
  const [delSuccess, setDelSuccess] = React.useState(false)
  const [ success, setSuccess ] = React.useState(false)
  const [openDialog, setOpenDialog] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [forUpDateData, setForUpdateData] = React.useState([])
  const [tableData, setTableData] = React.useState([])
  const [originalTableData, setOriginalTableData] = React.useState([])
  const [disableSave, setDisableSave] = React.useState(true)

  const [roomTypeItems, setRoomTypeItems] = React.useState([])
  const [roomItems, setRoomItems] = React.useState([])
  const [checkErrors, setCheckErrors] = React.useState({})

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchData()
    }

    return () => (mounted = false)
  }, [])

  const fetchData = async () => {
    try {
      const data = await ProfileService.getRoomAndBoard(paramId)
      const {
        data: { data: roomDesc }
      } = await RoomTypeService.get()

      setTableData(getNestedObjects(data, ['data', 'data', 'room_and_board']))
      setOriginalTableData(getNestedObjects(data, ['data', 'data', 'room_and_board']))
      setRoomItems(roomDesc)

      let arr = []

      roomDesc.map(items => {
        arr.push({
          id: getNestedObjects(items, ['_id']).trim(),
          text: `${getNestedObjects(items, ['desc']).trim()}`,
          value: `${getNestedObjects(items, ['desc']).trim()}`
        })
      })

      setRoomTypeItems(arr)
    } catch (e) {
      console.log('errors', e)
    }
  }

  const HandleSave = async () => {
    try {
      let obj = {}
      obj.id = paramId
      obj.roomAndBoard = [ ...tableData ]
      const responseData = await ProfileService.updateAndCreateRoomBoard(obj)

      setTableData(getNestedObjects(responseData, ['data', 'data', 'room_and_board']))
      setOriginalTableData(getNestedObjects(responseData, ['data', 'data', 'room_and_board']))
      setForUpdateData(getNestedObjects(responseData, ['data', 'data', 'room_and_board']))

      setSuccess(true)
      setOpenDialog(false)
      setIsToUpdate(false)
    } catch (e) {
      console.log('errors', e)
    }
  }

  React.useEffect(() => {
    try {
      if (tableData.some(items => items.roomType === '' ||
      items.description === '' || items.price === '.00' || items.price === 0 || items.price === null || items.price === '' || items.price === '0.00' || items.status === '')) {
        setDisableSave(true)
      } else if (!isEmpty(checkErrors)) {
        setDisableSave(true)
      } else {
        setDisableSave(false)
      }
    } catch (e) {
      console.log('error', e)
    }
  }, [tableData])

  const HandleCancel = () => {
    setTableData(originalTableData)
    setForUpdateData(originalTableData)
    setIsToUpdate(false)
    setIsCancel(false)
  }

  const HandleDelete = () => {
    setPassIndex(indexTodelete)
    setDelete(false)
    setTimeout(() => {
      setDelSuccess(true)
    }, 100)
  }

  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [addRoomType, setRoomType] = React.useState(false)

  const columns = Columns()
  const updateColumns = UpdateColumns(setIndexToDelete, setDelete)
  const data = React.useMemo(() => tableData, [tableData])

  return (
    <React.Fragment>
      <Modal open={open} aria-labelledby='simple-modal-title' aria-describedby='simple-modal-description'>
        <div id='root' className={classes.root}>
          <div id='container' className={classes.container}>
            <div id='Update-buttons' className={classes.updateButtons}>
              {!isToUpdate && <Button id='addRoomType' className={classes.addRoomType} onClick={() => setRoomType(true)} variant='contained'>
                    Add Room Type
              </Button> }
              <Button id='update' className={classes.updateButton} onClick={() => setIsToUpdate(true)} variant='contained'>
                    Update
              </Button>
            </div>
            <div id='tables' className={classes.tables}>
              {!isToUpdate && <ViewTable columns={columns} data={data} countName={'Room Types'} /> }
              {isToUpdate &&
              <UpdateTable
                columns={updateColumns}
                data={data}
                indexTodelete={passIndex}
                setPassIndex={setPassIndex}
                setTableData={setTableData}
                forUpDateData={forUpDateData}
                setForUpdateData={setForUpdateData}
                roomTypeItems={roomTypeItems}
                roomItems={roomItems}
                setCheckErrors={setCheckErrors}
                countName={'Room Types'}
              /> }
            </div>
            <div id='buttonDiv' className={classes.buttonBot}>
              {!isToUpdate &&
              <div id='closeButtonWrapper' className={classes.closeButtonWrapper}>
                <Button id='update' className={classes.closeButton} onClick={() => setOpen(false)} variant='contained'>
                    Close
                </Button>
              </div>
              }
              {isToUpdate && <div id='closeButtonAndSaveWrapper' className={classes.closeButtonAndSaveWrapper}>
                <Button id='closeUpdate' className={classes.closeButton} onClick={() => setIsCancel(true)} variant='contained'>
                    Cancel
                </Button>
                <Button id='saveButton' disabled={disableSave} className={disableSave ? classes.disableSave : classes.saveButton} onClick={() => setOpenDialog(true)} variant='contained'>
                    Save
                </Button>
              </div> }

            </div>
          </div>
        </div>
      </Modal>
      <div />
      <div />
      <AddRoomType open={addRoomType} setOpen={setRoomType} paramId={paramId} tableData={tableData} setForUpdateData={setForUpdateData} setTableData={setTableData} setOriginalTableData={setOriginalTableData} form={form} setForm={setForm} />
      <CustomConfirmationDialog open={isDelete}
        onApprove={HandleDelete}
        close={() => setDelete(false)}
        message='Are you sure you want to delete Room and Board?' />
      <ConfirmationBox setOpen={setOpen} openDialog={openDialog} setOpenDialog={setOpenDialog} HandleSave={HandleSave} />
      <CancelConfirmation isCancel={isCancel} setIsCancel={setIsCancel} HandleCancel={HandleCancel} />
      <CustomAlerSnackBar setDelay={800} isSave={success} setIsSave={() => setSuccess(false)} />
      {delSuccess && <CustomDeleteSnackbar isSave={delSuccess} setIsSave={() => setDelSuccess(false)} />}
    </React.Fragment>
  )
}

export default React.memo(Index)
