import React from 'react'
import { useTable, useFilters, useSortBy, usePagination, useGlobalFilter } from 'react-table'
import Table from '@material-ui/core/Table'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TextField from '../../../../../SharedComponents/TextField/CustomTextField'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import matchSorter from 'match-sorter'
import classes from '../scss/TableDisplay.module.scss'
import DropDown from '../../Components/CustomDrowpdown'

const DefaultColumnFilter = ({ column: { filterValue, setFilter } }) => {
  return (
    <div id='textField' className={classes.textField}>
      <TextField
        value={filterValue || ''}
        onChange={e => {
          setFilter(e.target.value || undefined) // Set undefined to remove the filter entirely
        }}
      />
    </div>
  )
}

const fuzzyTextFilterFn = (rows, id, filterValue) => {
  return matchSorter(rows, filterValue, { keys: [row => row.values[id]] })
}
fuzzyTextFilterFn.autoRemove = val => !val

const TableDisplay = props => {
  const filterTypes = React.useMemo(
    () => ({
      fuzzyText: fuzzyTextFilterFn,
      text: (rows, id, filterValue) => {
        const displayRows = rows.filter(row => {
          const rowValue = row.values[id]
          return rowValue !== undefined
            ? String(rowValue)
              .toLowerCase()
              .startsWith(String(filterValue).toLowerCase())
            : true
        })

        return displayRows
      }
    }),
    []
  )

  const defaultColumn = React.useMemo(
    () => ({
      // Let's set up our default Filter UI
      Filter: DefaultColumnFilter
    }),
    []
  )

  const { columns, data, setDownloadData, totalCounts, locationFilterData, fetchData, initialState, location = 'region', handleLocationFilter, isLoading } = props
  const {
    getTableProps, getTableBodyProps, headerGroups, prepareRow,
    page, // Instead of using 'rows', we'll use page,
    // which has only the rows for the active page

    // The rest of these things are super handy, too ;)
    rows,
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      filterTypes,
      manualPagination: true,
      autoResetPage: false,
      initialState: { pageIndex: initialState.pageIndex, pageSize: initialState.pageSize },
      pageCount: initialState.pageCount,
      // Be sure to pass the defaultColumn option
    },
    useFilters,
    useGlobalFilter,
    useSortBy,
    usePagination
  )
  // const LimitRows = rows.slice(0, 5)
  React.useEffect(() => {
    setDownloadData(rows)
  }, [page])

  React.useEffect(() => {
    gotoPage(0)
  }, [location])

  React.useEffect(() => {
    const skip = pageIndex * pageSize
    const offset = pageIndex * pageSize
    fetchData(location, skip, offset, pageSize, pageSize, pageIndex)
    setLocationFilterValue('')
  }, [location, pageIndex, pageSize])


  React.useEffect(() => {
    setDownloadData(rows)
  }, [])

  const [locationFilterValue, setLocationFilterValue] = React.useState(false)

  const handleDropdownSendRequest = ({ target: select }) => {
    setLocationFilterValue(select.value)
    handleLocationFilter(select.value)
  }



  return (
    <>
      <div className={classes.rootTableWraper} >
        <Table {...getTableProps()} className={classes.rootTable}>
          <TableHead className={classes.tableHead}>
            {headerGroups.map(headerGroup => (
              <TableRow {...headerGroup.getHeaderGroupProps()} className={classes.tableHeadRow}>
                {headerGroup.headers.map(column => (
                  <React.Fragment>
                    <TableCell
                      component='th'
                      scope='row'
                      align='left'
                      className={classes.tableCell}
                    >
                      {column.canFilter ? (
                        <div id='withTextfield' className={classes.withTextfield}>
                          {column.render('Header')}
                          <span id='showIcon' className={classes.showIcon} {...column.getHeaderProps(column.getSortByToggleProps())}>
                            {column.isSortedDesc ? (
                              <div id='sortIcon' className={classes.sortIcon}>
                                <FontAwesomeIcon icon='sort' />
                              </div>
                            ) : (
                                <div id='sortIcon' className={classes.sortIcon}>
                                  <FontAwesomeIcon icon='sort' />
                                </div>
                              )}
                          </span>
                          <div>
                            {locationFilterData && <DropDown onChange={handleDropdownSendRequest} items={locationFilterData} value={locationFilterValue} label={''} />}
                            {!locationFilterData && column.render('Filter')}
                          </div>
                        </div>
                      ) : (
                          <div id='noTextField' className={classes.noTextField}>
                            {column.render('Header')}
                          </div>
                        )}
                    </TableCell>
                  </React.Fragment>
                ))}
              </TableRow>
            ))}
          </TableHead>

          <TableBody {...getTableBodyProps()} >
            {page.map((row, i) => {
              prepareRow(row)
              return (
                <TableRow key={i} {...row.getRowProps()} className={classes.tableRow}>
                  {row.cells.map(cell => {
                    return (
                      <TableCell
                        component='th'
                        scope='row'
                        align='left'
                        className={classes.tableCell}
                        {...cell.getCellProps()}
                      >
                        {cell.render('Cell')}
                      </TableCell>
                    )
                  })}
                </TableRow>
              )
            })}
            <TableRow className={classes.totalFooter} style={{backgroundColor: '#9ea3ac'}}>
              {totalCounts.map((count, i) => (
                 <TableCell
                 component='th'
                 scope='row'
                 align='left'
                 key={`count-${i}`}
                 className={classes.tableCell}
                 style={{color: 'white'}}
               >
                 {count}
               </TableCell>
              ))}
            </TableRow>
          </TableBody>
        </Table>
      </div>
      <div className={classes.Pagination}>
        <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
          {'<<'}
        </button>{' '}
        <button onClick={() => previousPage()} disabled={!canPreviousPage}>
          {'<'}
        </button>{' '}
        <button onClick={() => nextPage()} disabled={!canNextPage}>
          {'>'}
        </button>{' '}
        <button onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
          {'>>'}
        </button>{' '}
        <span>
          Page{' '}
          <strong>
            {pageIndex + 1} of {pageOptions.length}
          </strong>{' '}
        </span>
        <select
          value={pageSize}
          onChange={e => {
            setPageSize(Number(e.target.value))
          }}
        >
          {[5, 10, 20].map(pageSize => (
            <option key={pageSize} value={pageSize}>
              Show {pageSize}
            </option>
          ))}
        </select>
      </div>
    </>
  )
}

// export default React.memo(TableDisplay)
export default React.memo(TableDisplay, (prevProps, nextProps) => {
  try {
    if(
      prevProps.data === nextProps.data &&
      prevProps.pageIndex === nextProps.pageIndex &&
      prevProps.location === nextProps.location &&
      prevProps.pageSize === nextProps.pageSize
      ) {
      return true
    }
  } catch (error) {
    return false
  }
})
