import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'status',
  'claim_type',
  'discount_application',
  'payment_type',
  'terms_in_days',
  'rate',
  'effectivity_date',
  'end_date',
  'discount_to_be_deducted_from',
  'discount_type',
  'other_details_of_discount'
]

const schema = Joi.object({
  status: Joi.string()
    .label('Status'), // required
  discount_type: Joi.string()
    .label('Discount Type'),
  claim_type: Joi.string()
    .label('Claim Type'), // required
  discount_application: Joi.string()
    .label('Discount Application'), // required
  included_procedure: Joi.array()
    .label('Included Procedure').required(), // required
  payment_type: Joi.string()
    .label('Payment Type'), // required
  terms_in_days: Joi.number()
    .label('Terms in days'), // required
  rate: Joi.number()
    .label('Rate'), // required
  effectivity_date: Joi.string()
    .label('Effectivity Date'), // required
  end_date: Joi.string()
    .label('End Date'), // required
  discount_to_be_deducted_from: Joi.string()
    .label('Discount to be deducted from'), // required
  other_details_of_discount: Joi.string()
    .label('Other details of discount'),
  addProcedure: Joi.string().label('Procedure').messages({'any.required': `"Procedure" is required`}).required()
})

export default function AddDiscountValidation (form, targetValue) {
  let errors = {}

  console.log('form', form)

  const errorMessageForProcedure = (errors) => {
    if (targetValue === 'discount_application' || targetValue === 'other_details_of_discount') {
      delete errors['addProcedure']
      if (form['discount_application'] === 'Per Procedure') {
        delete errors['addProcedure']
        if (form['included_procedure'].length === 0) {
          errors['addProcedure'] = 'Procedure is a required'
        } else {
          delete errors['addProcedure']
        }
      }
    } else {
      if (form['discount_application'].length > 0 || form['other_details_of_discount'].length > 0) {
        delete errors['addProcedure']
        if (form['discount_application'] === 'Per Procedure') {
          delete errors['addProcedure']
          if (form['included_procedure'].length === 0) {
            errors['addProcedure'] = 'Procedure is a required'
          } else {
            delete errors['addProcedure']
          }
        }
      }
    }
  }

  const validateIfEmployeeDiscount = errors => {
    if (form && form['discount_type'] === 'Early Payment Discount') {
      if (form && form['terms_in_days'].length > 0) {
        if (form && Number.isInteger(Number(form['terms_in_days']))) {
          delete errors['terms_in_days']
        } else {
          errors['terms_in_days'] = 'Terms in days must be an integer'
        }
      }
    }
  }

  const result = schema.validate(form, { abortEarly: false })

  const taxRatePattern = new RegExp(/^(\d{0,2}(\.\d{1,5})?|100(\.00?)?)$/)

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      if (targetValue === 'discount_type' || targetValue === 'other_details_of_discount') {
        if (form['discount_type'].length > 0 || form['other_details_of_discount'].length > 0) {
          errors[i.path[0]] = i.message
          delete errors['addProcedure']

          if (form['rate'].length > 0) {
            if (!taxRatePattern.test(form['rate'])) {
              errors['rate'] = 'Tax Rate must not exceed 100%'
            } else {
              delete errors['rate']
            }
          }

          validateIfEmployeeDiscount(errors)
          errorMessageForProcedure(errors)
        }
      } else {
        if (form['discount_type'].length > 0 || form['other_details_of_discount'].length > 0) {
          errors[i.path[0]] = i.message
          delete errors['addProcedure']

          if (form['rate'].length > 0) {
            if (!taxRatePattern.test(form['rate'])) {
              errors['rate'] = 'Tax Rate must not exceed 100%'
            } else {
              delete errors['rate']
            }
          }

          validateIfEmployeeDiscount(errors)
          errorMessageForProcedure(errors)
        }
      }
      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
  }

  console.log('errors', errors)

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
