import React from 'react'
import classes from './Number.module.scss'
import NumberFormat from 'react-number-format'
import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

let specialChar = RegExp(/[!@#$%^&*(),.?":;'`~_{}|<>]/)
let lettersOnly = RegExp(/^[a-z]+$/)

const DisableCharacters = (e, type) => {
  switch (type) {
    case 'integerOnly':
      if (
        e.key === 'e' ||
        e.key === 'E' ||
        e.keyCode === 190 ||
        e.keyCode === 189 ||
        e.keyCode === 111 ||
        e.keyCode === 106 ||
        e.keyCode === 107 ||
        e.keyCode === 109 ||
        e.keyCode === 187 ||
        e.keyCode === 219 ||
        e.keyCode === 191 ||
        e.keyCode === 221 ||
        e.keyCode === 220 ||
        e.keyCode === 222 ||
        specialChar.test(e.key) ||
        lettersOnly.test(e.key)
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    default:
      return {}
  }
}

const schema = Joi.object({
  price: Joi.number().label('Price').required()
})

const Validation = (form) => {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message
    }
  }
  if (form && form['price'] === '0.00') {
    errors['price'] = '"Price" must be a safe number'
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}

const NumberFormatComponents = props => {
  const {
    error,
    onChange,
    value,
    setErrors,

    name,
    decimalSeparator,
    type,
    decimalScale,
    onBlur,
    format
  } = props

  React.useEffect(() => {
    const { errors, isValid } = Validation({price: value})
    if (isValid) {
      setErrors({})
    } else {
      setErrors(errors)
    }
  }, [value])

  return (
    <React.Fragment>
      <div className={error ? classes.InputFieldsError : classes.InputFields}>
        <NumberFormat
          value={value}
          name={name}
          type={type}
          onChange={onChange}
          onBlur={onBlur}
          isNumericString
          displayType={'input'}
          decimalScale={decimalScale}
          fixedDecimalScale
          allowLeadingZeros={false}
          decimalSeparator={decimalSeparator}
          className={classes.textField}
          autoComplete='off'
          onKeyDown={e => DisableCharacters(e, type)}
          format={format}
          allowEmptyFormatting
        />
        <div className={classes.error}> {error && error}</div>

      </div>
    </React.Fragment>
  )
}

export default React.memo(NumberFormatComponents, (prevProps, nextProps) => {
  try {
    if (prevProps.value === nextProps.value && prevProps.name === nextProps.name && prevProps.items === nextProps.items && prevProps.onChange === nextProps.onChange) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
