import React from 'react'
import CheckIcon from '@material-ui/icons/Check'
import CancelIcon from '@material-ui/icons/Cancel'
import classes from './InlineText.module.scss'
import TextField from '@material-ui/core/TextField'
import moment from 'moment'
import TableCell from '@material-ui/core/TableCell'

const InlineForm = props => {
  const { x } = props
  const [values, setValues] = React.useState(x)
  const [errors, setErrors] = React.useState({})

  const change = e => {
    let newErrrors = { ...errors }
    const { name, value } = e.target
    newErrrors[name] = undefined
    setValues({ ...values, [name]: value })
    setErrors(newErrrors)
  }

  const validate = () => {
    let newErrors = {...errors}
    for (let key in values) {
      if (values[key] === '') {
        newErrors[key] = 'Required Field'
      }
    }

    setErrors(newErrors)
    return Object.values(values).includes('')
  }

  const onSubmit = e => {
    e.preventDefault()
    const err = validate()
    if (!err) {
      props.handleSave(props.i, values)
    }
  }

  const { header, i } = props

  return (
    <>
    {header.map((y, k) => (
      <TableCell>
        <div style={{display: 'flex', flexDirection: 'column', width: '100%'}}>
          <TextField
            id='outlined-basic'
            name={y.prop}
            type={y.isDatePicker ? 'date' : 'text'}
            onChange={change}
            className={errors[y.prop] ? classes.textEditError : classes.textEdit}
            // value={y.isDatePicker ? '2017-05-24' : values[y.prop]}
            value={y.isDatePicker ? moment(values[y.prop]).format('YYYY-MM-DD') : values[y.prop]}
            // defaultValue='2017-05-24'
            errorText={errors[y.prop]}
            variant='outlined'
          />
          {errors[y.prop] && <span style={{color: 'red'}}> {errors[y.prop]} </span>}
        </div>
      </TableCell>
      ))}
      <TableCell>
        <div className={classes.checkIcon}>
          <CheckIcon onClick={onSubmit} />
          <CancelIcon onClick={props.stopEditing} />
        </div>
      </TableCell>
    </>
  )
}

export default InlineForm
