import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import InputField from '../InputField'
import SelectInput from '../DropDown'
import RoomTypeService from '../../../../../../../../../Services/Api/RoomType/RoomType'
import Validation from './validation'
import debounce from 'lodash/debounce'
import { getNestedObjects } from '../../../../../../Components/Utils/utils'
import CustomAlerSnackBar from '../../../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import ProfileService from '../../../../../../../../../Services/Api/Partner/ProfileService'

const IndexComponents = props => {
  const { open, paramId, setOpen, form, setForm, isToUpdate, tableData, setForUpdateData, setTableData, setOriginalTableData } = props

  const [errors, setErrors] = React.useState({})
  const [roomTypeItems, setRoomTypeItems] = React.useState([])
  const [roomTypeFullItems, setRoomTypeFullItems] = React.useState([])
  const [ success, setSuccess ] = React.useState(false)

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      Fetch()
    }

    return () => (mounted = false)
  }, [])

  const Fetch = async () => {
    const {
      data: { data: roomDesc }
    } = await RoomTypeService.get()

    setRoomTypeFullItems(roomDesc)

    let arr = []

    roomDesc.map(items => {
      arr.push({
        id: getNestedObjects(items, ['_id']).trim(),
        text: getNestedObjects(items, ['desc']).trim(),
        value: getNestedObjects(items, ['desc']).trim()
      })
    })

    setRoomTypeItems(arr)
  }

  const HandleChange = (value, name) => {
    const newErrors = { ...errors }
    const newForm = { ...form }
    delete newErrors[name]
    newForm[name] = value
    setErrors(newErrors)
    setForm(newForm)
  }

  const HandleCreate = () => {
    const { errors, isValid } = Validation(form)

    if (isValid) {
      setErrors({})
      Create()
    } else {
      setErrors(errors)
    }
  }

  const Create = async () => {
    try {
      const roomCodeResult = roomTypeFullItems.filter(items => items.desc.trim() === form['roomType'].trim())
      let tableDataToSend = [...tableData, {
        roomCode: getNestedObjects(roomCodeResult, ['0', 'code']),
        roomType: form['roomType'],
        description: form['description'],
        price: form['price'],
        status: 'Active',
        remarks: form['remarks']
      }]

      let obj = {}
      obj.id = paramId
      obj.roomAndBoard = [ ...tableDataToSend ]

      const responseData = await ProfileService.updateAndCreateRoomBoard(obj)
      setTableData(getNestedObjects(responseData, ['data', 'data', 'room_and_board']))
      setOriginalTableData(getNestedObjects(responseData, ['data', 'data', 'room_and_board']))
      setForUpdateData(getNestedObjects(responseData, ['data', 'data', 'room_and_board']))

      setSuccess(true)
      ClearForm()
      setOpen(false)
    } catch (e) {
      console.log('errors', e)
    }
  }

  const ClearForm = () => {
    setForm({
      roomCode: '',
      roomType: '',
      description: '',
      price: '',
      status: '',
      remarks: ''
    })
  }

  const HandleCancel = () => {
    setOpen(false)
    setForm({
      roomCode: '',
      roomType: '',
      description: '',
      price: '',
      status: '',
      remarks: ''
    })
    setErrors({})
  }

  const debounceChange = debounce((value, name) => HandleChange(value, name), 0)

  return (
    <div id='root'>
      <Dialog open={open} fullWidth maxWidth='md' aria-labelledby='max-width-dialog-title'>
        <DialogTitle id='form-dialog-title' style={{color: 'lightgray', fontSize: '15px', fontWeight: '900'}}>ADD ROOM TYPE</DialogTitle>
        <DialogContent id='addRoomContent' style={{display: 'flex', width: '100%', flexDirection: 'row', height: 'auto', marginTop: '3em', marginBottom: '3em'}}>
          <div style={{width: '40%', marginRight: '1em'}}>
            <label style={{color: '#1e2071'}}>Room Type*</label>
            <div>
              <SelectInput items={roomTypeItems} name={'roomType'} onChange={({ target: { value, name } }) => debounceChange(value, name)} value={form['roomType']} error={errors['roomType']} />
            </div>
          </div>
          <div style={{width: '60%', height: 'auto', marginRight: '1em', marginLeft: '1em'}}>
            <label style={{color: '#1e2071'}}>Room Description*</label>
            <div>
              <InputField name={'description'} onChange={({ target: { value, name } }) => debounceChange(value, name)} value={form['description']} error={errors['description']} />
            </div>
          </div>
          <div style={{width: '40%', marginLeft: '1em'}}>
            <label style={{color: '#1e2071'}}>Remarks</label>
            <div>
              <InputField name={'remarks'} onChange={({ target: { value, name } }) => debounceChange(value, name)} value={form['remarks']} error={errors['remarks']} />
            </div>
          </div>
        </DialogContent>
        <DialogActions style={{marginRight: '3em', marginBottom: '2em'}}>
          <Button onClick={HandleCancel} style={{borderRadius: '10px', marginRight: '0.5em', color: 'white', backgroundColor: '#fd5451'}} variant='contained'>
          Cancel
          </Button>
          <Button onClick={HandleCreate} style={{borderRadius: '10px', marginLeft: '0.5em', color: 'white', backgroundColor: '#1ab1c4'}} variant='contained'>
         CREATE
          </Button>
        </DialogActions>

      </Dialog>
      <CustomAlerSnackBar setDelay={800} isSave={success} setIsSave={() => setSuccess(false)} />
    </div>
  )
}

const Index = React.memo(IndexComponents, (prevProps, nextProps) => {
  try {
    if (prevProps.form === nextProps.form && prevProps.items === nextProps.items && prevProps.value === nextProps.value && prevProps.open === nextProps.open) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})

export default Index
