import React from 'react'
import InputBase from '@material-ui/core/InputBase'
import classes from './TextField.module.scss'
import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'remarks'
]

const schema = Joi.object({
  description: Joi.string().label('Room Description').required(),
  remarks: Joi.string().label('Remarks').required()
})

const Validation = (form) => {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message

      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}

const IndexComponents = props => {
  const { value, style, inputRef, setErrors, onChange, error, type, name, multiline } = props

  React.useEffect(() => {
    const { errors, isValid } = Validation({description: value, remarks: value})
    if (isValid) {
      setErrors({})
    } else {
      setErrors(errors)
    }
  }, [value])

  return (
    <React.Fragment>
      <div className={error ? classes.InputFieldsError : classes.InputFields}>
        <InputBase
          value={value}
          name={name}
          style={style}
          className={error ? classes.InputFieldsError : classes.InputFields}
          type={type}
          multiline={multiline}
          inputRef={inputRef}
          onChange={onChange}
          autoComplete='off'
        />
      </div>
      { error && <span className={classes.error}> { error }</span> }
    </React.Fragment>
  )
}

const TextField = React.memo(IndexComponents, (prevProps, nextProps) => {
  try {
    if (prevProps.value === nextProps.value && prevProps.onChange === nextProps.onChange && prevProps.error === nextProps.error) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})

export default TextField
