import React from 'react'
import Table from './Table/Index'

const EditableTable = ({
  data: tdata,
  columns,
  indexTodelete,
  forUpDateData,
  setPassIndex,
  setForUpdateData,
  setTableData,
  roomTypeItems,
  roomItems,
  setCheckErrors,
  countName
}) => {
  const [data, setData] = React.useState(tdata)
  const [tmpData, setTmpData] = React.useState(tdata)

  React.useEffect(() => {
    setData(data => data.filter((items, index) => index !== indexTodelete))
    setTmpData(data => data.filter((items, index) => index !== indexTodelete))
    if (setPassIndex) {
      setPassIndex(-1)
    }
  }, [indexTodelete])

  React.useEffect(() => {
    setForUpdateData(tdata)
  }, [tdata])

  React.useEffect(() => {
    setTableData(data)
  }, [data, indexTodelete])

  const updateMyData = (rowIndex, columnName, value) => {
    setTmpData(previousData =>
      previousData.map((data, i) => {
        if (i === rowIndex) {
          return {
            ...data,
            [columnName]: value
          }
        } else {
          return data
        }
      })
    )

    setData(previousData =>
      previousData.map((data, i) => {
        if (i === rowIndex) {
          return {
            ...data,
            [columnName]: value
          }
        } else {
          return data
        }
      })
    )
  }

  return (
    <React.Fragment>
      <Table
        columns={columns}
        data={data}
        updateMyData={updateMyData}
        setForUpdateData={setForUpdateData}
        roomTypeItems={roomTypeItems}
        roomItems={roomItems}
        setCheckErrors={setCheckErrors}
        countName={countName}
      />
    </React.Fragment>
  )
}

export default React.memo(EditableTable)
