import React from 'react'
import { useTable, useFilters, useGlobalFilter, useSortBy, usePagination } from 'react-table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Pagination from '@material-ui/lab/Pagination'
import classes from './Index.module.scss'

import FilterTypesComponent from '../../Utilities/Filter'
import DefaultColumnComponents from '../DefaultColumn'

const ReactTableComponents = props => {
  const filterTypes = FilterTypesComponent()
  const defaultColumn = DefaultColumnComponents()

  const { columns, data, countName, updateMyData, roomTypeItems, roomItems, setCheckErrors } = props

  const [currentPage, setCurrentPage] = React.useState(0)

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // Instead of using 'rows', we'll use page,
    // which has only the rows for the active page

    // The rest of these things are super handy, too ;)
    //  canPreviousPage,
    //   canNextPage,
    pageOptions,
    //  pageCount,
    gotoPage,
    //  nextPage,
    // previousPage,
    setPageSize,
    state: { pageIndex, pageSize }
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      filterTypes,
      updateMyData,
      roomTypeItems,
      roomItems,
      setCheckErrors,
      autoResetPage: false,
      initialState: {
        pageIndex: 0,
        pageSize: 10
      },
      disableSortRemove: true

      // Be sure to pass the defaultColumn option
    },
    useFilters,
    useGlobalFilter,
    useSortBy,
    usePagination
  )

  const HandlePagination = (e, value) => {
    const page = value ? Number(value) - 1 : 0
    setCurrentPage(page + 1)
    gotoPage(page)
  }

  return (
    <div id='root' className={classes.root}>
      <TableContainer component={Paper}
        {...getTableProps()}
        aria-label='a dense table'
        className={classes.viewTable}
      >
        <TableHead id='TableHeadRoot' className={classes.TableHeadRoot}>
          {headerGroups.map((headerGroup, index) => (
            <TableRow key={index} {...headerGroup.getHeaderGroupProps()} className={classes.tableHeadRow}>
              {headerGroup.headers.map((column, index) => (
                <TableCell key={index} scope='row' align='left' className={classes.tableCell}>
                  {column.canFilter ? (
                    <div id='withTextfield' className={classes.withTextfield}>
                      {column.render('Header')}
                      <span
                        id='showIcon'
                        className={classes.showIcon}
                      >
                        {column.isSortedDesc ? (
                          <div id='sortIcon' className={classes.sortIcon} {...column.getHeaderProps(column.getSortByToggleProps())}>
                            <FontAwesomeIcon icon='sort' />
                          </div>
                        ) : (
                          <div id='sortIcon' className={classes.sortIcon} {...column.getHeaderProps(column.getSortByToggleProps())}>
                            <FontAwesomeIcon icon='sort' />
                          </div>
                        )}
                      </span>
                      <div> {column.render('Filter')} </div>
                    </div>
                  ) : column.Header === 'deleteIcon' ? (
                    ''
                  )
                    : (
                      <div id='noTextField' className={classes.noTextField}>
                        {column.render('Header')}
                      </div>
                    )}
                </TableCell>
              ))}
            </TableRow>
          ))}
        </TableHead>
        <TableBody id='tableBody' className={classes.tableBody}>
          <div {...getTableBodyProps()}>
            {page.map((row, i) => {
              prepareRow(row)
              return (
                <TableRow key={i} {...row.getRowProps()} className={classes.tableRow}>
                  {row.cells.map((cell, index) => {
                    return (
                      <TableCell
                        key={index}
                        component='th'
                        align='left'
                        scope='row'
                        className={classes.tableCell}
                        {...cell.getCellProps()}
                      >
                        {cell.render('Cell')}
                      </TableCell>
                    )
                  })}
                </TableRow>
              )
            })}
          </div>
        </TableBody>
      </TableContainer>

      <div className={classes.paginationDiv}>
        <div id='pageCount' className={classes.pageCount}>
          {page.length} - {pageSize} Out of {data.length} {countName}
        </div>
        <div className={classes.Pagination}>
          <select
            value={pageSize}
            onChange={e => {
              setPageSize(Number(e.target.value))
            }}
            style={{ marginLeft: '1.5em' }}
          >
            {[10, 15, 20].map(pageSize => (
              <option key={pageSize} value={pageSize}>
                Show {pageSize}
              </option>
            ))}
          </select>

          <Pagination
            count={pageOptions.length}
            page={currentPage}
            onChange={HandlePagination}
            showFirstButton
            showLastButton

          />
        </div>
      </div>
    </div>
  )
}

const ReactTable = React.memo(ReactTableComponents, (prevProps, nextProps) => {
  try {
    if (
      prevProps.page === nextProps.page &&
      prevProps.data === nextProps.data &&
      prevProps.columns === nextProps.columns &&
      prevProps.headerGroups === nextProps.headerGroups &&
      prevProps.updateMyData === nextProps.updateMyData
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})

export default ReactTable
