import React from 'react'
import Button from '@material-ui/core/Button'
import CustomDialogSave from '../../../../../Components/CustomDialogSave/CustomDialogSave'

const ConfirmationBox = props => {
  const { setOpen, openDialog, setOpenDialog, HandleSave, disableSave } = props

  return (
    <CustomDialogSave
      onClose={() => {
        setOpen(false)
      }}
      id='a_sos_custom_dialog'
      isSave={openDialog}
      // setIsSave={() => setIsSave(false)}
    >
      <div id='a_sos_custom_dialog_container'>
        <h3 id='a_sos_custom_dialog_message'>Are you sure you want to update?</h3>
        <div id='a_sos_custom_dialog_btn_group' >
          <Button
            onClick={() => setOpenDialog(false)}
            id='a_sos_custom_dialog_btn_no'
            variant='contained'
            style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
          >
                No
          </Button>
          <Button
            disabled={disableSave}
            onClick={HandleSave}
            id='a_sos_custom_dialog_btn_yes'
            variant='contained'
            style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
          >
                Yes
          </Button>
        </div>
      </div>
    </CustomDialogSave>
  )
}

export default ConfirmationBox
