import React from 'react'

const Columns = () => {
  const columns = React.useMemo(
    () => [
      {
        Header: 'Room Code',
        accessor: 'roomCode'
      },
      {
        Header: 'Room Type',
        accessor: 'roomType'
      },
      {
        Header: 'Room Description',
        accessor: 'description'
      },
      {
        Header: 'Price',
        accessor: 'price',
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'Status',
        accessor: 'status',
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'Remarks',
        accessor: 'remarks',
        disableFilters: true,
        disableSortBy: true
      }
    ],
    []
  )

  return columns
}

export default Columns
