import React from 'react'
import Select from './Inputs/DropDown'
import TextField from './Inputs/TextField/TextField'
import NumberFormat from './Inputs/NumberFormat/NumberFormat'

import debounce from 'lodash/debounce'

const EditableCell = ({
  value: initialValue,
  row: { index },
  column: { id },
  data = [],
  updateMyData,
  roomTypeItems,
  roomItems,
  setCheckErrors // This is a custom function that we supplied to our table instance
}) => {
  // We need to keep and update the state of the cell normally
  const [value, setValue] = React.useState(initialValue)

  const [roomCodeAndType, setRoomCodeAndType] = React.useState({
    roomCodeValue: '',
    roomTypeValue: ''
  })

  const [errors, setErrors] = React.useState({})

  console.log('errors', errors)

  const items = [ { id: '11', text: 'Active', value: 'Active' }, { id: '22', text: 'Inactive', value: 'Inactive' } ]

  React.useEffect(() => {
    setValue(initialValue)
  }, [initialValue])

  const HandleChange = value => {
    setValue(value)
  }

  const HandleTextField = e => {
    setValue(e.target.value)
  }

  const HandleRoomType = (e, index) => {
    if (e.target.value.length > 0) {
      const result = roomItems.filter(items => { if (items.desc === e.target.value) { return items } })
      setValue(e.target.value)
      const newRoomType = { ...roomCodeAndType }
      newRoomType['roomCodeValue'] = result[0].code
      updateMyData(index, 'roomCode', result[0].code)
      setRoomCodeAndType(newRoomType)
    } else {
      setValue(e.target.value)
      const newRoomType = { ...roomCodeAndType }
      newRoomType['roomCodeValue'] = ''
      updateMyData(index, 'roomCode', '')
      setRoomCodeAndType(newRoomType)
    }
  }

  React.useEffect(() => {
    updateMyData(index, id, value)
  }, [value])

  React.useEffect(() => {
    setCheckErrors(errors)
  }, [errors])

  const debounceChange = debounce((value) => HandleChange(value), 10)

  return ['roomCode'].includes(id) ? (
    <span>{value}</span>
  ) : ['status'].includes(id) ? (<Select items={items} name={'status'} setErrors={setErrors} error={errors['status']} onChange={({ target: { value } }) => debounceChange(value)} value={value} />)
    : ['roomType'].includes(id) ? (<Select items={roomTypeItems} name={'roomType'} setErrors={setErrors} error={errors['roomType']} onChange={e => HandleRoomType(e, index)} value={value} />)
      : ['description'].includes(id) ? (<TextField name={'description'} setErrors={setErrors} error={errors['description']} onChange={HandleTextField} value={value} multiline />)
        : ['remarks'].includes(id) ? (<TextField name={'remarks'} setErrors={setErrors} error={errors['remarks']} onChange={({ target: { value } }) => debounceChange(value)} value={value} multiline />)
          : ['price'].includes(id) ? (<NumberFormat name={'price'} setErrors={setErrors} error={errors['price']} decimalScale={2} type={'integerOnly'} onChange={({ target: { value } }) => debounceChange(value)} value={value} />) : ''
}

export default React.memo(EditableCell, (prevProps, nextProps) => {
  try {
    if (prevProps.value === nextProps.value && prevProps.roomTypeItems === nextProps.roomTypeItems && prevProps.initialValue === nextProps.initialValue && prevProps.updateMyData === nextProps.updateMyData) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
