import React from 'react'
import DeleteIcon from '@material-ui/icons/Delete'

const UpdateColumns = (setIndexToDelete, setDelete) => {
  const getData = (e, props) => {
    const indexValue = props.row.index

    setIndexToDelete(indexValue)
    setDelete(true)
  }

  const updatecolumns = React.useMemo(
    () => [
      {
        Header: 'Room Code',
        accessor: 'roomCode'
      },
      {
        Header: 'deleteIcon',
        accessor: 'deleteIcon',
        Cell: props => (
          <div id='cpt'>
            <DeleteIcon id='del' style={{ width: '20px', height: '20px', marginTop: '0.3em', display: 'none' }} onClick={e => getData(e, props)} /> {props.value}
          </div>
        ),
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'Room Type',
        accessor: 'roomType'
      },
      {
        Header: 'Room Description',
        accessor: 'description'
      },
      {
        Header: 'Price',
        accessor: 'price',
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'Status',
        accessor: 'status',
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'Remarks',
        accessor: 'remarks',
        disableFilters: true,
        disableSortBy: true
      }
    ],
    []
  )

  return updatecolumns
}

export default UpdateColumns
