import React from 'react'
import ReactTable from '../Helpers/TableDisplayApiPagination'
// import ReactTable from '../Helpers/TableDisplay'
import { totalMedicalPartnersCol } from '../../../../../Constant/Reports/ColumnsHeaders'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import classes from '../scss/TableDisplay.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import DropDown from '../../Components/CustomDrowpdown'

const locationCategories = [
  { id: 1, text: 'Region', value: 'Region', searchString: 'region' },
  { id: 2, text: 'Province', value: 'Province', searchString: 'province' },
  { id: 3, text: 'City/Municipality', value: 'City/Municipality', searchString: 'city' },
  { id: 4, text: 'Barangay', value: 'Barangay', searchString: 'barangay' }
]

const MedicalPartners = () => {
  const [medicalPartners, setMedicalPartners] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [totalCounts, setTotalCounts] = React.useState([])
  const [locationFilters, setLocationFilters] = React.useState([])

  const [openPreview, setOpenPreview] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const [previousData, setPrivousData] = React.useState([])
  const [medicalDoctorLocationFilter, setMedicalDoctorLocationFilter] = React.useState('Region')
  const [location, setLocation] = React.useState('region')
  const [initialState, setInitialState] = React.useState({
    pageIndex: 0,
    pageSize: 10,
    pageCount: 0,
    skip: 0
  })

  React.useEffect(() => {
    fetch()
  }, [])

  const handleDropdown = async ({ target: { value = '' } }) => {
    let val = locationCategories.find(data => data.value === value)
    // fetch(val.searchString)
    setMedicalDoctorLocationFilter(value)
    setLocation(val.searchString)
    setInitialState({
      pageIndex: 0,
      pageSize: 10,
      pageCount: 0,
      skip: 0
    })
  }

  const handleLocationDropdown = data => {
    if (data instanceof Array) {
      let text = ''
      let newRegions = data.map(loc => {
        let obj = {}
        if (loc.city) {
          text = `${loc.name}, ${loc.city}`
          obj.value = `${loc.name}, ${loc.city}`
          obj.searchString = `${loc.city} / ${loc.name}`
        } else if (loc.province) {
          text = `${loc.name}, ${loc.province}`
          obj.searchString = `${loc.province} / ${loc.name}`
          obj.value = `${loc.name}, ${loc.province}`
        } else {
          text = loc.name
          obj.searchString = loc.name
          obj.value = loc.name
        }
        obj.id = loc._id
        obj.text = text
        return obj
      })
      setLocationFilters(newRegions)
    }
  }

  const handleLocationFilter = (searchString = '') => {
    const newData = [...previousData]
    let newSearchString = locationFilters.find(data => data.value === searchString)
    const res = newData.filter(data => {
      return data.location.indexOf(newSearchString.searchString) !== -1
    })
    setMedicalPartners(res)
  }

  const fetch = async (category = 'region', skip = 0, offset = 0, limit = 10, pageSize = 10, pageIndex) => {
    setIsLoading(true)
    try {
      const init = {...initialState}
      const { data: { data: { data: finalData, count, totalCounts, locationFilter } } } = await ReportServices.getTotalMedicalPartnersCounts({ category, skip, offset, limit })
      init.pageCount = Math.ceil(count / pageSize)
      init.pageIndex = pageIndex
      setInitialState(init)
      handleLocationDropdown(locationFilter)
      setTotalCounts(totalCounts)
      setPrivousData(finalData)
      setMedicalPartners(finalData)
      setIsLoading(false)
    } catch (error) {
      setIsLoading(false)
    }
  }

  return (
    <div>
      <div className={classes.rightBody}>
        <div style={{ width: '200px' }}>
          <DropDown onChange={handleDropdown} items={locationCategories} value={medicalDoctorLocationFilter} label='Location Category' />
        </div>
        <div id='root' className={classes.root} style={{ marginTop: '20px' }}>
          <ReactTable
            columns={totalMedicalPartnersCol}
            data={medicalPartners}
            setDownloadData={setDownloadData}
            locationFilterData={locationFilters}
            handleLocationFilter={handleLocationFilter}
            medicalDoctorLocationFilter={medicalDoctorLocationFilter}
            fetchData={fetch}
            initialState={initialState}
            location={location}
            totalCounts={totalCounts}
            isLoading={isLoading}
          />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtnMedical}>Download Preview</Button>
        <PreviewDownload openPreview={openPreview} setOpenPreview={setOpenPreview} downloadData={downloadData} headerColumn={totalMedicalPartnersCol} filename={`${moment().format('YYYYMMDD')}-medical-partners.csv`} />
      </div>
    </div>

  )
}

export default MedicalPartners
