
import Select from '@material-ui/core/Select'
import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'
import classes from './index.module.scss'
import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'roomCode',
  'price'
]

const schema = Joi.object({
  status: Joi.string().label('Status').required(),
  roomType: Joi.string().label('Room Type').required()
})

const Validation = (form) => {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message

      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}

const SelectInput = props => {
  const {
    id,
    required,
    items = [],
    readOnly,
    onChange,
    setErrors,

    disableComponents = false,
    error,
    name,
    minWidth,
    ...other
  } = props
  const { value = '' } = props

  const [_error, setError] = React.useState()
  const [isEmp, setisEmp] = React.useState(false)

  React.useEffect(() => {
    if (typeof value === 'string') {
      if (value === '') {
        setisEmp(true)
      } else {
        setisEmp(false)
      }
      if (!value.trim()) setError(error)
    }
  }, [error, value])

  React.useEffect(() => {
    const { errors, isValid } = Validation({status: value, roomType: value})
    if (isValid) {
      setErrors({})
    } else {
      setErrors(errors)
    }
  }, [value])

  return (
    <div className={classes.root}>
      <div className={classes.wrapper} style={minWidth}>
        <React.Fragment>
          <Paper
            className={`${classes['select-container']} ${_error && !value && classes['has-error']} ${
              disableComponents && classes.disabled
            }`}
          >
            <Select
              id={id}
              onChange={onChange}
              readOnly={readOnly}
              displayEmpty
              value={value}
              renderValue={(value) => isEmp ? '' : value}
              classes={{ root: classes.select }}
              input={<InputBase className={classes.input} />}
              disabled={disableComponents}
              {...other}
            >
              <MenuItem value={``} />
              {items.map((item, index) => (
                <MenuItem key={`${id}-items${index}`} id={`${id}-items${index}`} value={item.value}>
                  {item.text}
                </MenuItem>
              ))}
            </Select>
          </Paper>

          {_error && !value && <span className={classes.error}>{_error}</span>}
        </React.Fragment>
      </div>
    </div>
  )
}

export default SelectInput
